import React from 'react';

import { DragConstraint, Layer, Frame, Drag } from '../../proto';

export default class Example extends React.Component {
  state = {
    x: 0,
    y: 0,
  };

  _onDragEnd = ({ x, y }) => {
    this.setState({ x, y });
  };

  render() {
    return (
      <Layer
        frame={Frame(50, (667 - 275) / 2, 275, 275)}
        style={{
          backgroundColor: '#c3f0f7',
        }}
      >
        <Layer
          frame={Frame(
            Drag(this.state.x, {
              min: 0,
              max: 275 - 80,
              bounce: true,
              momentum: true,
            }),
            Drag(this.state.y, {
              min: 0,
              max: 275 - 80,
              bounce: true,
              momentum: true,
            }),
            80,
            80
          )}
          onDragEnd={this._onDragEnd}
          style={{
            backgroundColor: '#1693A5',
          }}
        />
      </Layer>
    );
  }
}



